<template>
	<view>
		<!-- 通知 -->
		<uni-notice-bar style="z-index: 1" show-icon scrollable text="本次活动最终解释权归管理员所有!" />
		<!-- 图片 -->
		<view class="page" style="margin-top: -10px;">
			<view class="image-list">
				<view class="image-item">
					<view class="image-content">
						<image style="width: 100%; height: 380px; background-color: #eeeeee;" mode="scaleToFill"
							:src="sendData.awardUrl">
						</image>
					</view>
				</view>
			</view>
		</view>
		<!-- 介绍卡片 -->
		<view class="">
			<uni-card>
				<!-- 积分 -->
				<image style="width: 18px;height: 18px;margin-top: 10px;" mode="aspectFit"
					src="https://mp-891a44a8-f007-4426-a7a4-607ad2bc43d0.cdn.bspapp.com/cloudstorage/bcb52907-c887-42e2-9fd1-f78834701e8b.png"></image>
				<text class="text">{{sendData.awardintegral}}</text>
				<image style="width: 15px;height: 15px;margin-top: 10px;margin-left: 20px;" mode="aspectFit"
					src="https://mp-891a44a8-f007-4426-a7a4-607ad2bc43d0.cdn.bspapp.com/cloudstorage/4fd1d27c-fb04-40b0-8fd5-62ac31a4c08f.png"></image>
				<text style="text-decoration: line-through;color: #bfbfbf;">免费\n</text>
				<view class="reduction">
					<view class="reduction_1">
						满599减50
					</view>
					<view class="reduction_1">
						满499减40
					</view>
					<view class="reduction_1">
						满399减30
					</view>
					<view class="reduction_1">
						...
					</view>
					
				</view>
				<!-- 标题 -->
				<text class="uni-body titleText">{{sendData.awardintroduce}}</text>
				
				<view class="member">
					<view class="member_1">
						<text>加入会员，享受会员价</text>
					</view>
					<view class="member_1">
						<text>了解更多</text>
					</view>
				</view>
				<view style="margin-top: 15px;color: #bfbfbf;">
					已售0件
				</view>
			</uni-card>
		</view>
		<!-- 按钮导航 -->
		<view class="goods-carts">
			<uni-goods-nav :options="options" :fill="true" :button-group="buttonGroup" @click="onClick"
				@buttonClick="buttonClick" />
		</view>
	</view>
</template>

<script>
	// import notice from '../../../uni_modules/uni-notice-bar/components/uni-notice-bar/uni-notice-bar.vue'
	// import uGoodsNav from '../../../uni_modules/uni-goods-nav/components/uni-goods-nav/uni-goods-nav.vue'
	// import uCard from '../../../uni_modules/uni-card/components/uni-card/uni-card.vue'
	export default {
		data() {
			return {
				sendData: null,
				options: [{
						icon: 'shop',
						text: '店铺',
						infoBackgroundColor: '#007aff',
						infoColor: "#f5f5f5"
					},
					{
						icon: 'cart',
						text: '购物车',
					}
				],
				buttonGroup: [{
					text: '立即兑换',
					backgroundColor: 'linear-gradient(90deg, #FE6035, #EF1224)',
					color: '#fff'
				}],
				customButtonGroup: [{
					text: '立即兑换',
					backgroundColor: 'linear-gradient(90deg, #60F3FF, #088FEB)',
					color: '#fff'
				}],
				customButtonGroup1: [{
					text: '立即兑换',
					backgroundColor: 'linear-gradient(90deg, #FE6035, #EF1224)',
					color: '#fff'
				}]
			}
		},
		components: {
			// uGoodsNav,
			// notice,
			// uCard
		},
		onLoad() {
			// 接收跳转时传过来的数据
			const eventChannel = this.getOpenerEventChannel();
			eventChannel.on('sendData', (data) => {
				this.sendData = data
				
			})


		},
		methods: {
			onClick(e) {
				if (e.content.text == '店铺') {
					uni.navigateBack()
				} else {
					uni.showToast({
						title: `暂无${e.content.text}`,
						icon: 'none'
					})
				}


			},
			buttonClick() {
				// 个人总积分
				let sendInt=this.sendData.awardintegral
				let Integral = this.$store.state.Integral
				let that=this
				if (Integral >= sendInt) { //判断个人积分是否大商品积分
				let endIntegral =Integral - sendInt
					uni.request({
						url: 'https://1el9898253.oicp.vip/exchange/',
						method: "post",
						data: {
							integral: sendInt,
							name: this.sendData.awardName
						},
						success: () => {
							//需改vuex个人积分
							that.$store.commit('EDIT',endIntegral)
							uni.navigateBack({
								success:()=>{
									uni.showToast({
										title: '兑换成功！',
										icon: 'none',
									})
								}
							})
						}
					});
					
				} else { //个人积分小于商品积分
					uni.showModal({
						title: '提示',
						content: '积分不足！',
					})
				}
				
			}
		}
	}
</script>

<style>
	.example-body {
		padding: 0;
		/* #ifndef APP-NVUE */
		display: block;
		/* #endif */
	}

	.goods-carts {
		/* #ifndef APP-NVUE */
		display: flex;
		/* #endif */
		flex-direction: column;
		position: fixed;
		left: 0;
		right: 0;
		/* #ifdef H5 */
		left: var(--window-left);
		right: var(--window-right);
		/* #endif */
		bottom: 0;
	}
	
	.text {
		font-size: 30px;
		/* font-weight: bold; */
		color: rgb(245, 119, 118);
		letter-spacing: -3px;
		/* 文字间距 */
		margin-top: -6px;
		
	}
	.titleText{
		
		color: black;
		font-size: 17px;
		display: inline-block;
		margin-top: 15px;
	}
	
	.reduction{
		display: flex;
		justify-content: space-between;
		width: 90%;
	}
	.reduction_1{
		background-color: rgb(255, 236, 236);
		border-radius: 3px;
		color: rgb(245, 119, 118);
		width: fit-content; /* 宽度自适应 */
		padding: 0 5px;
		margin-top: 15px;
	}
	.member{
		background-color: rgb(250, 242, 216);
		border-radius: 5px;
		display: flex;
		justify-content: space-between;
		height: 40px;
		margin-top: 15px;
		padding: 0 15px;
		line-height: 40px;
	}
</style>
